import React, { useEffect, useState } from "react"
import Button from "./button"
import Carousel from "./carousel"
import { isDesktopScreen } from 'lib/helpers'
import { select } from 'lib/selectors'

function ThankYouPage(props)
{
    const { thankYouHeaderMessage, isBA, userOrderBtn, thankYouHeaderMessageUser, sharedProductInfo, sharedProductInfoUser, addedProducts, cartUrl, thankYouMsgOne, thankYouMsgTwo, thankYouMsgThree } = jsConfigSecond.twilio
    const [isDesktop, setIsDesktop] = useState(isDesktopScreen())
    const { sharedProduct } = props
    var addedTobagCount = 0

    const resizeHandler = () =>
    {
        setIsDesktop(isDesktopScreen())
    }
    sharedProduct.map(item =>
    {
        if (item.addToCartDisabled) {
            addedTobagCount += 1
        }
    })
    const baText = `${thankYouMsgOne}
                    ${thankYouMsgTwo} ${addedTobagCount ? addedTobagCount : ""} ${thankYouMsgThree}`

    useEffect(() =>
    {
        window.addEventListener('resize', resizeHandler)
        select('.thankyou__container_heading').focus()
        !isBA && select(".thankyou__container").classList.add("thankyou__container__client")
        return (() =>
        {
            window.removeEventListener('resize', resizeHandler)
        })
    }, [])

    return (
        <React.Fragment>
            <div className="thankyou__container">
                <h1 className="thankyou__container_heading heading" tabIndex="-1" focusable="true">
                    {!isBA && addedTobagCount > 0 && isDesktop ? baText : isBA ? thankYouHeaderMessage : thankYouHeaderMessageUser}
                </h1>
                {isBA ? <p className="thankyou__container_description_ba"> {`${sharedProduct.length} ${sharedProductInfo}`} </p> : <p className="thankyou__container_description"> {sharedProductInfoUser} </p>}

                {!isBA && (
                    <div className="context">
                        <a href={cartUrl} className="button is-primary is-block">
                            <span>{userOrderBtn}</span>
                        </a>
                    </div>
                )}
                {!isBA && addedTobagCount > 0 ? (
                    <div aria-live="polite" aria-atomic="true">
                        <p className="thankyou__container_addedProducts">
                            {addedTobagCount} {addedProducts}
                        </p>
                    </div>
                ) : (
                    <div aria-live="polite" aria-atomic="true"></div>
                )}
            </div>

            <Carousel sharedProductList={sharedProduct} addedTobagCount={addedTobagCount} />
        </React.Fragment>
    );
}
export default ThankYouPage
