import React, { Fragment, useEffect, useState, useRef } from "react"
import Button from "./button"
import SwiperCore, { Pagination, Navigation } from "swiper"
SwiperCore.use([Pagination, Navigation])
import { isDesktopScreen, focusElement, isMobileScreen } from "lib/helpers"
import { getImage } from '../Utils/helper'
import WishListIcon from './wishListIcon'
import { getCollection, select } from 'lib/selectors'
import Icon from "./Icon"
import { Swiper, SwiperSlide } from 'swiper/react';


function Carousel(props) {
  const { sharedProductList } = props
  const { isBA, sharedMsg, sharedMsgBA, sharedProdCountText, addToCartCTA, productAddedText, productRef, carouselPrev, carouselNext, carouselPager } = jsConfigSecond.twilio
  
  const [isDesktop, setIsDesktop] = useState(isDesktopScreen())
 const [isMobile, setIsMobile] = useState(isMobileScreen())
  const defaultImg = `${config.theme}/assets/media/product_default.png`
  const verticalbar_img = `${config.theme}/assets/media/vertical-bar.png`
  const isBoutiqueApp = window.jsConfigSecond.isBoutiqueApp
  const prevRef = useRef(null)
  const nextRef = useRef(null)
  const swiperRef = useRef(null)

  const resizeHandler = () => {
    setIsDesktop(isDesktopScreen())
    setIsMobile(isMobileScreen())
  }

  useEffect(() => {
    window.addEventListener('resize', resizeHandler)
      focusElement(select('.thankyou__container_heading'))
      afterRenderSwiper(null, 4000) 
    return (() => {
      window.removeEventListener('resize', resizeHandler)
    })
  }, [])

  const BuildHTML = (props) => {
    var item = props.carouselItem
    var index = props.index

    if (item) {
      const productImages = item.images || (item.basic && item.basic.images)
      const priceValue = `${item.price && item.price.formattedValue ? item.price.formattedValue : item.price ? item.price : ''}`
      return (
        <div className="slider__card" tabIndex="-1">
          <div className="slider__card__imageColumn">
            {(productImages && <img width="160" height="160" src={`${getImage(productImages)}`} alt='' />) || <img width="160" height="160" src={defaultImg} alt='' />}
          </div>
          <div className="slider__card__contentColumn" tabIndex="-1">
            {!isBA && !isBoutiqueApp && <WishListIcon pCode={item.code} pName={item.name} isWishlist={item.isInWishList} index={index} />}
            <div role='heading' aria-level='3' id={`product-list-${index}-${item.code}`} className="slider__card__contentColumn_prod-info">
              {item.name && (
                <span role='presentation' className={` ${isBA ? 'slider__card__contentColumn__productName_ba' : 'slider__card__contentColumn__productName'}`}>
                  {item.name.toLowerCase()}
                </span>
              )}
              {item.description && (
                <span role='presentation' className="slider__card__contentColumn__productContent">
                  {item.description.toLowerCase()}
                </span>
              )}
              <span className='is-sr-only'>{productRef + " " + item.code}</span>
            </div>
            <p className="slider__card__contentColumn__priceTag">
              {priceValue}
            </p>
            {!isBA && !item.addToCartDisabled && !isBoutiqueApp && <Button buttonClass="slider__card__contentColumn__prodMsg" dataPopupTarget="quickview" ariaDescribedby={`product-list-${index}-${item.code}`} dataPopupQuery={`sku=${item.code}&quickView=true`} ariaHasPopup="dialog" dataModalTitle={item.name} buttonText={<span>{addToCartCTA}</span>} />}
            {!isBA && item.addToCartDisabled && !isBoutiqueApp && <p tabIndex="-1" className="slider__card__contentColumn__cartMsgText">{productAddedText}</p>}
          </div>
        </div>
      )
    }
    else {
      return (
        <Fragment></Fragment>
      )
    }
  }

  const BuildContainer = (props) => {
    return (
      <Fragment>
        <BuildHTML carouselItem={props.prod} index={props.index} />
      </Fragment>
    )
  }


  const afterRenderSwiper = (swiper = null, timeGap) => {
    if (swiper) {
     setA11y()
    } else {
      setTimeout(() => {
        setA11y()
      }, timeGap)
    }
  }

  const setA11y = () => {
    const pagerCurrent = select('.js-carousel-pager-current')
    const prevBtn = select('.js-carousel-prev')
    const nextBtn = select('.js-carousel-next')
    if (pagerCurrent)
     isDesktop ? pagerCurrent.innerText = swiperRef && swiperRef.current.swiper.activeIndex + 1 : ''
    if (swiperRef.current.swiper.activeIndex === 0) {
      prevBtn && prevBtn.setAttribute('disabled', '')
      nextBtn && nextBtn.removeAttribute('disabled')
      nextBtn && nextBtn.removeAttribute('hidden')
      prevBtn && prevBtn.setAttribute('hidden', '')
      }
    else if (isDesktop && swiperRef.current.swiper.activeIndex === Math.ceil(sharedProductList.length / 3 - 1)) {
      prevBtn && prevBtn.removeAttribute('disabled')
      nextBtn && nextBtn.setAttribute('disabled', '')
      prevBtn && prevBtn.removeAttribute('hidden')
      nextBtn && nextBtn.setAttribute('hidden', '')
    }
    else if (isMobile && (swiperRef.current.swiper.activeIndex + 1 === sharedProductList.length)) {
      nextBtn && nextBtn.setAttribute('disabled', '')
      nextBtn && nextBtn.setAttribute('hidden', '')
    }
    else {
      prevBtn && prevBtn.removeAttribute('disabled')
      nextBtn && nextBtn.removeAttribute('disabled')
      prevBtn && prevBtn.removeAttribute('hidden')
      nextBtn && nextBtn.removeAttribute('hidden')
    }
      let disableIcons = [...getCollection(".wishlist-icon"), ...getCollection(".slider__card__contentColumn__prodMsg")]
      let activeSlidesContainer = select('.swiper-slide-active')
      let carouselSlides = getCollection('.slider__card')
      carouselSlides && carouselSlides.forEach(slide => {
        if (activeSlidesContainer.contains(slide)) {
          slide.removeAttribute('aria-hidden')
          slide.removeAttribute('tabindex')
        } else {
          slide.setAttribute('aria-hidden', 'true')
          slide.setAttribute('tabindex', '-1')
        }
      })
      disableIcons && disableIcons.forEach(icons => {
        if (activeSlidesContainer.contains(icons)) {
          icons.removeAttribute('tabindex')
        } else {
          icons.setAttribute('tabindex', '-1')
        }
      })
  }

  const focusActiveSlide = (isNextBtnClicked) => {
    let activeSlideBtns = getCollection('.swiper-slide-active .slider__card')
    if (isNextBtnClicked) {
      focusElement(activeSlideBtns[0])
    } else {
      isDesktop ? focusElement(activeSlideBtns[2]) : focusElement(activeSlideBtns[0])
    }
  }

  return (
    <Fragment>
      <div className="vertical-bar">
        <img src={verticalbar_img} width="1" height="36" alt="" />
      </div>
      <div className="carousal__container">
        <div role="heading" aria-level="2" id="product-list" className="carousal__container__shared_products">
          {!isBA ? sharedMsg : sharedMsgBA}
        </div>
        <p className="carousal__container__products">
          {sharedProductList.length} {sharedProdCountText}
        </p>
        <div className="js-carousel carousal__container_inner">
          
            <Fragment>
              <Swiper ref={swiperRef}
                pagination={{
                  type: "fraction",
                  el: 'swiper-pagination',
                }}
                slidesPerView={isDesktopScreen() ? "1" : 'auto'}
                navigation={{
                  // Both prevEl & nextEl are null at render so this does not work
                  prevEl: prevRef.current,
                  nextEl: nextRef.current,
                }}
                a11y={true}
                speed={0}
                className="carousal__container_swiper carousel__outer"
                onSlideChangeTransitionEnd={(swiper) => afterRenderSwiper(swiper, null)}
                onInit={(swiper) => {
                  swiper.params.navigation.prevEl = prevRef.current
                  swiper.params.navigation.nextEl = nextRef.current
                }}
              >
                {sharedProductList &&
                  sharedProductList.map((carouselItem, index) => {
                    if (isDesktop && !sharedProductList[3 * index + 0]) {
                      return
                    }
                    return (
                      <SwiperSlide key={`prod ${index + 1}`}>
                        <div className="carousal__container_swiper__slide">
                          {!isDesktop && <BuildContainer prod={carouselItem} />}
                          {isDesktop && (
                            <Fragment>
                              <BuildContainer prod={sharedProductList[3 * index + 0]} index={3 * index + 1} />
                              <BuildContainer prod={sharedProductList[3 * index + 1]} index={3 * index + 2} />
                              <BuildContainer prod={sharedProductList[3 * index + 2]} index={3 * index + 3} />
                            </Fragment>
                          )}
                        </div>
                      </SwiperSlide>
                    )
                  })}
              </Swiper>
              {isDesktop && <p className="swiper-pagination js-carousel-pager swiper-pagination-fraction">
                <span className="swiper-pagination-current js-carousel-pager-current">1</span>
                <span aria-hidden="true">/</span>
                <span className="is-sr-only">{carouselPager}</span>
                <span className="swiper-pagination-total js-carousel-pager-total">{Math.ceil(sharedProductList.length / 3)}</span>
              </p>}
              <div className="swiper-navigation js-carousel-navigation">
                <button type="button" onClick={() => !isDesktop && swiperRef.current.swiper.slidePrev()} className={isDesktop ? 'swiper-button swiper-button-prev js-carousel-prev' : 'swiper-button swiper-button-prev js-carousel-prev is-sr-only-focusable'} ref={prevRef} onClick={() => focusActiveSlide(false)}>
                  <Icon alt={carouselPrev} name={'next-white'} size={'large'} />
                </button>
                <button type="button" className={isDesktop ? 'swiper-button swiper-button-next js-carousel-next' : 'swiper-button swiper-button-next js-carousel-next is-sr-only-focusable'} ref={nextRef} onClick={() => focusActiveSlide(true)}>
                  <Icon alt={carouselNext} name={'next-white'} size={'large'} />
                </button>
              </div>
            </Fragment>
          
        </div>
      </div>
    </Fragment>
  )
}

export default Carousel
